*{box-sizing:border-box}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;width:50%;margin:0 auto;display:flex;align-items:center;justify-content:center;min-height:100vh;background:#151515}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}


*{paint-order:stroke}

button{display:inline-flex;border:none;padding:5px;margin:0;text-decoration:none;color:#ffffff;font-family:sans-serif;font-size:1rem;cursor:pointer;text-align:center;-webkit-transition:background 250ms ease-in-out,  -webkit-transform 150ms ease;transition:background 250ms ease-in-out,  -webkit-transform 150ms ease;transition:background 250ms ease-in-out,  transform 150ms ease;transition:background 250ms ease-in-out,  transform 150ms ease,  -webkit-transform 150ms ease;-webkit-appearance:none;-moz-appearance:none}button:focus{outline:0}button:active{-webkit-transform:scale(0.99);transform:scale(0.99)}

